#gjs-context-menu
    min-width: 200px
    position: absolute
    top: 0
    left: 0
    z-index: 90
    transform: scale(0)
    border-radius: 5px

    *
        box-sizing: border-box

    // &.dragging
    //     background-color: red
    // &.dragging &__header *
    //     pointer-events: none

    &.visible
        transform: scale(1)
        transform-origin: top left
        animation-name: show-context-menu
        animation-duration: 200ms
        animation-timing-function: ease-in-out

.gcm
    &__header
        background: var( --ui-color-3, #252525 )
        border-radius: 5px 5px 0 0
        overflow: hidden
        flex-wrap: wrap
        &__row
            display: flex
            justify-content: space-between
        &__title
            cursor: move
            padding: 8px 10px
            color: var( --font-color, #9c9a9a )
        &__actions
            display: flex
            button
                padding: 0 3px
                border: 0
                background-color: transparent
                color: var( --font-color, #9c9a9a )
                cursor: pointer
                &:hover
                    color: var( --brand-color-1, #fff )
                &.disabled
                    opacity: .3
                    pointer-events: none
            i
                pointer-events: none
                font-size: 18px
        .breadcrumbs-wrapper
            background-color: var( --ui-color-4, #343434 )
                
        &__breadcrumbs
            padding: 6px 10px
            p
                margin: 0
                font-size: 11px
            a
                color: var( --brand-color-1, #fff )
                text-decoration: none

    &__actions
        h5
            background-color: var( --ui-color-3, #252525 )
            text-align: center
            font-size: 10px
            letter-spacing: 1px
            margin: 0
            color: var( --font-color, #9c9a9a )
            padding: 3px 15px
        .title-left,
        .title-right
            display: flex
            h5
                white-space: nowrap
                display: flex
                align-items: center
        .title-right
            flex-direction: row-reverse

    &__color-picker
        .sp-palette-container
            display: none
        .sp-container
            border: 0
            background-color: var( --ui-color-3, #252525 )
            border-radius: 5px
        .sp-picker-container
            border-left: 0
        .sp-input
            border: 0
            background-color: var( --ui-color-1, #151515 )
            color: var( --font-color, #9c9a9a )
            outline: 0
        .sp-clear
            bottom: -50px
            top: initial
            transform: scale(.7)
        .sp-hue
            top: 0
            height: 100%
        
    &-item
        width: 100%
        border: 0
        padding: 8px 10px
        background-color: var( --ui-color-2, #1c1c1c )
        color: var( --font-color, #9c9a9a )
        border-top: 1px solid var( --ui-color-4, #343434 )
        cursor: pointer
        text-align: center
        outline: 0
        font-size: 11px
        white-space: nowrap
        &.active
            color: var( --brand-color-1, #fff )
            cursor: default
        i
            vertical-align: bottom
            font-size: 18px
        &.disabled
            pointer-events: none
            opacity: .8
            background-color: var( --ui-color-1, #151515 )
            color: var( --ui-color-4, #343434 )

    &-distribute-box
        display: flex
        justify-content: center
        background-image: linear-gradient(135deg, var(--ui-color-2, #1c1c1c) 12.50%, var( --ui-color-1, #151515) 12.50%, var( --ui-color-1, #151515) 50%, var(--ui-color-2, #1c1c1c) 50%, var(--ui-color-2, #1c1c1c) 62.50%, var( --ui-color-1, #151515) 62.50%, var( --ui-color-1, #151515) 100%)
        background-size: 5.66px 5.66px
        &>div
            flex: 1
            &:not(:last-child)
                .gcm-item
                    border-right: 1px solid var( --ui-color-4, #343434 )
        button, a
            min-height: 40px
            font-size: 11px
        &.column
            flex-direction: column
            background-image: initial
            justify-content: flex-start
            &>div
                flex: initial
        &-wrapper &-wrapper
            background-color: var( --ui-color-2, #1c1c1c )
    
    &-item-toggle
        padding: 0
        text-align: center
        i
            font-size: 15px
            transition: 500ms transform
            pointer-events: none
        &.active i
            transform: rotate(180deg)

    &-range
        &-wrapper
            display: flex
            align-items: center
            input
                width: 100%
        &-output
            font-size: 11px
            min-width: 23px

    &-item-color
        span
            border: 1px solid var( --font-color, #9c9a9a )
            display: block
            height: 20px
            pointer-events: none

    &-item-input
        min-height: 40px
        font-size: 11px
        -moz-appearance: textfield
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button
            -webkit-appearance: none
        &--small
            max-width: 80px

@-webkit-keyframes show-context-menu
    0%
        transform: scale(0)
    100%
        transform: scale(1)